import React from 'react'
import store from "../../redux/store";

export const isDeviceConnected = () => {
	const devices = store.getState().devicesReducer.devices || {}

	for(const device of Object.values(devices)) {
		const {
			connected
		} = device || {}
		if(connected) return true
	}

	return false
}

